
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    colorHex: {
      type: String,
      default: "#2e3440", // nord0
    },
  },
  data: function () {
    return {
      inverted: false,
      width: 100,
      height: 2,
    };
  },
  created: function () {
    this.inverted = Math.random() > 0.5;
    this.width = 100 + Math.random() * 200;
    this.height = 2 + Math.random() * 2;
  },
});
